
	:root{
        --overLay: #000;
    }
    
    *{
		margin: 0;
		box-sizing: border-box;
	}

	@font-face{
	font-family: 'NixieOne';
	src: url('../fonts/nixie-one-v11-latin-regular.woff2');
	}

    .page{
       height: 1000vh;
       position: relative;
    }
    .image{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    } 
	.image svg text{
		position: absolute;
		top: 40%;
		left: 50%;
		z-index: 0;
	}
